<template>
    <div>
        <div class="login">
            <flash-component></flash-component>

            <h1>{{title}}</h1>

            <form>
                <div class="form-group">
                    <label for="email">Email address</label>
                    <input class="form-control" id="email" type="text" name="email"
                           v-model="fields.email"
                           @keydown="updateErrorEmail"
                           @keyup.enter="login"
                    >
                </div>
                <div v-if="errors && errors.email" class="text-danger">{{ errors.email[0] }}</div>

                <div class="form-group">
                    <label for="password">Password</label>
                    <input class="form-control" id="password" type="password" name="password"
                           v-model="fields.password"
                           @keydown="updateErrorPassword"
                           @keyup.enter="login"
                    >
                </div>
                <div v-if="errors && errors.password" class="text-danger">{{ errors.password[0] }}</div>

                <button class="btn btn-primary" @click.prevent="login">Login</button>

            </form>
        </div>
    </div>

</template>

<script>

    import Flash from "./Flash";
    import { mapState} from 'vuex';
    import { mapGetters} from 'vuex';

    export default {
        data: function (){
            return {
                fields: {}
            }
        },

        created: function(){
            if(this.id>0){
                this.$router.push('dashboard')
            }
        },
        mounted: function(){
            this.$parent.$parent.$refs.myAdmin.style.marginLeft = '0px';
        },
        destroyed: function(){
            this.$parent.$parent.$refs.myAdmin.style.marginLeft = '255px';
        },

        computed: {
            ...mapState('general',[
                'title'
            ]),
            ...mapGetters('flash',[
                'errors'
            ]),
            ...mapGetters('auth',[
                'id'
            ])
        },

        components: {
            flashComponent: Flash
        },
        methods: {
            updateErrorEmail(){
                // this.errors.email = '';
            },
            updateErrorPassword(){
                // this.errors.password = '';
            },

            login() {
                this.$store.dispatch('flash/updateErrors', '');
                if(!this.fields.email){
                    this.$store.dispatch(
                        'flash/updateErrorKey',
                        (['email',['E-mail field is required.']])
                    );
                }
                if(this.fields.email && this.fields.email.length<3){
                    this.$store.dispatch(
                        'flash/updateErrorKey',
                        (['email',['E-mail must contain at least 3 chars.']])
                    );
                }
                if(!this.fields.password){
                    this.$store.dispatch(
                        'flash/updateErrorKey',
                        (['password',['Password field is required.']])
                    );
                }
                if(this.fields.password && this.fields.password.length<3){
                    this.$store.dispatch(
                        'flash/updateErrorKey',
                        (['password',['Password must contain at least 3 chars.']])
                    );
                }

                if(Object.keys( this.errors ).length === 0) {
                    axios.post('/my-admin/login', this.fields).then(response => {


                        this.$store.dispatch('auth/updateId', response.data.id);
                        this.$router.push('dashboard');

                    }).catch(error => {
                        this.$store.dispatch('auth/updateId', 0);

                        if (error.response.status === 422) {
                            this.$store.dispatch('flash/updateErrors', error.response.data.errors || {});


                        }

                    });

                }

            }
        },
    }
</script>

<style>

    .login{
        margin: 100px auto;
        width: 50%;
        border: 1px solid #ebebeb;
        padding: 20px;
        background-color: #f8f8f8;
    }
    @media screen and (max-width: 768px) {
        .login{
            width: 100%;
        }
    }
</style>
