<template>
    <div>
        <div v-if="display">
            <div v-if="flash && flash.error" class="alert alert-danger">{{ flash.error }}</div>
            <div v-else-if="flash && flash.success" class="alert alert-success">{{ flash.success }}</div>
            <div v-else></div>
        </div>
    </div>
</template>

<script>


    import { mapGetters } from 'vuex';
    import { mapActions } from 'vuex';

    export default {
        computed: {
            ...mapGetters('flash',[
                'flash','display'
            ]),
        }
    }
</script>
