<template>
    <div>
        <label>Tags</label>

        <div class='tag-box'>
            <div v-for='(tag, index) in tags' :key='tag' class='tag-item'>
                <span @click='removeTag(index)'>x</span>
                {{ tag.toLowerCase() | capitalize}}
            </div>
            <input
                    type='text'
                    placeholder="Enter a Tag"
                    class='tag-input'
                    @keydown.enter='addTag'
                    @keydown.188='addTag'
                    @keydown.delete='removeLastTag'
            />
        </div>

    </div>
</template>
<script>

    import { moduleName } from './moduleConfig.js';

    import { mapGetters } from 'vuex';

    export default {
        computed: {
            ...mapGetters(moduleName,[
                'tags'
            ])
        },
        methods: {
            addTag (event) {
                event.preventDefault()
                let val = event.target.value.trim();

                //check if tag already exists, then remove
                let index = this.tags.indexOf(val);
                if (index > -1) {
                    this.tags.splice(index, 1);
                }

                if (val.length > 0) {
                    this.tags.push(val)
                    event.target.value = ''
                }
            },
            removeTag (index) {
                this.tags.splice(index, 1)
            },
            removeLastTag(event) {
                if (event.target.value.length === 0) {
                    this.removeTag(this.tags.length - 1)
                }
            }
        }
    }
</script>
<style scoped>
    .tag-box {
        width: 100%;
        border: 1px solid #eee;
        font-size: 0.9em;
        box-sizing: border-box;
        padding: 0 10px;
        margin-bottom: 20px;
    }

    .tag-item {
        float: left;
        margin-top: 3px;
        margin-right: 10px;
        background-color: #3a6d9c;
        padding: 6px 15px;
        border-radius: 5px;
        color: #fefefe;
    }

    .tag-item > span {
        cursor: pointer;
        opacity: 0.75;
    }

    .tag-input {
        border: none;
        outline: none;
        font-size: 0.9em;
        background: none;
        padding: 6px 15px;
        color: #1c1c1c;
    }

</style>
